.header {
    display: flex;
    padding: 5px 10px;
    justify-content: space-between;
    position: sticky;
    background-color: white;
    z-index: 100;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.header__left {
display: flex;
justify-content: space-between;
}

.header__input {
    display: flex;
    align-items: center;
    background-color: #eff2f5;
    padding: 10px;
    margin-left: 10px;
    margin-top: 10px;
    border-radius: 999px;
    height: 20px;
}

.header__input > input {
    border: none;
    background-color:  transparent;
    outline-width: 0;
}

.header__center {
    display: flex;
    flex: 1;
    justify-content: center;
}

.header__left > img {
    height: 60px;
}

.header__option > .MuiSvgIcon-root {
    color: gray;
}

.header__option:hover > .MuiSvgIcon-root {
    color: #2e81f4;
}

.header__option-active > .MuiSvgIcon-root{
    color: #2e81f4;
}

.header__option-active{
    border-bottom:4px solid #2e81f4;
    border-radius: 2px;
}


.header__option {
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
}

.header__option:hover{
    background-color: #eff2f5;
    border-radius: 10px;
    padding: 0 30px;
    align-items: center;
    border-bottom:none ;
}

.header__info{
    display: flex;
    align-items: center;
}

.header__info > h4 {
    margin-left: 10px;

}

.header__right{
    display: flex;
    padding: 0 20px;
}